@charset "UTF-8";
* {
  box-sizing: border-box;
  outline: none; }

*::before, *::after {
  box-sizing: inherit; }

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0; }

ul, ol {
  margin: 0; }
  ul.no-style, ol.no-style {
    padding: 0;
    list-style: none; }

button {
  outline: none;
  border: none; }

img {
  border: none; }

a {
  text-decoration: none;
  color: inherit; }

[contenteditable=true]:empty:before {
  content: attr(data-placeholder);
  color: #131313;
  font-size: inherit;
  display: block;
  /* For Firefox */ }

select {
  appearance: none; }
  select::-ms-expand {
    display: none; }

input[type='number'] {
  -moz-appearance: textfield; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none; }

blockquote {
  margin: 0; }

h1 {
  font-size: 60px; }

h2 {
  font-size: 24px;
  line-height: 1.2; }

h3 {
  font-size: 18px;
  line-height: 24px; }

body,
*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  touch-action: manipulation; }

body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Roboto-Regular", sans-serif;
  font-size: 16px;
  line-height: 1.6;
  overflow-x: hidden; }
  @media screen and (max-width: 991px) {
    body {
      font-size: 14px;
      line-height: 24px; } }
  @media screen and (max-width: 480px) {
    body {
      font-size: 12px;
      line-height: 20px; } }

.container {
  max-width: 1180px;
  width: 100%;
  margin: auto;
  padding: 0 20px; }
  @media screen and (max-width: 767px) {
    .container {
      padding: 0 28px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.apple-fix {
  width: 100%;
  position: fixed; }

.no-scroll {
  overflow: hidden; }

.img-wrapper {
  display: inline-block; }
  .img-wrapper img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.clearfix::after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  position: relative; }
  .wrapper .header {
    flex: 0 0 auto; }
  .wrapper main {
    position: relative;
    flex: 1 0 auto; }
  .wrapper .footer {
    flex: 0 0 auto; }

button {
  cursor: pointer; }

.header {
  padding-top: 33px;
  padding-bottom: 63px;
  max-width: 1680px;
  background-image: url(../img/bg-hero.png);
  background-repeat: no-repeat;
  background-size: cover; }
  .header__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 67px; }
  .header__nav {
    display: flex;
    justify-content: space-between;
    max-width: 780px; }
    .header__nav-link {
      margin-right: 26px;
      color: #222222; }
      .header__nav-link:nth-child(4) {
        margin-right: 0; }
  .header__phone {
    display: flex;
    flex-direction: column; }
  .header__number {
    color: #444444;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    margin-bottom: 10px; }
  .header__back-call {
    background: #FF5C5C;
    border-radius: 2px;
    padding: 0 15px;
    line-height: 26px;
    color: #FFFFFF;
    font-family: "Roboto", sans-serif;
    font-size: 14px; }
  .header__screen {
    max-width: 654px;
    margin-bottom: 40px; }
    .header__screen-lead {
      font-size: 32px;
      line-height: 37px;
      color: #444444;
      margin-bottom: 60px; }
      .header__screen-lead--bold {
        font-weight: bold; }
    .header__screen-text {
      font-size: 23px;
      line-height: 32px;
      color: #444; }

.borisych {
  width: 500px;
  margin: auto; }

